import { _decorator, AudioClip, AudioSource, Component, Node, Toggle, sys } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('AudioToggle')
export class AudioToggle extends Component {
    static instance: AudioToggle;

    @property(AudioClip)
    public clips: AudioClip [] = [];

    @property(Toggle)
    public audioTog: Toggle = null;
    @property(Node)
    public turnOnA: Node = null;
    @property(Node)
    public turnOffA: Node = null;
    @property(AudioSource)
    public audioSrc: AudioSource = null;

    @property(Toggle)
    public musicTog: Toggle = null;
    @property(Node)
    public turnOnM: Node = null;
    @property(Node)
    public turnOffM: Node = null;
    @property(AudioSource)
    public musicSrc: AudioSource = null;

    private audioStatus: boolean = false;
    private musicStatus: boolean = false;

    protected onLoad(): void {
        AudioToggle.instance = this;
        this.audioTogFunc();
    }

    onAudioQueue(index: number){
        let clip: AudioClip = this.clips[index];
        this.audioSrc.playOneShot(clip);
    }

    audioTogFunc(){
        this.onAudioQueue(2);
        // Khôi phục trạng thái âm thanh từ Local Storage khi scene được load
    let savedStatus = sys.localStorage.getItem('audioStatus');
    if (savedStatus === 'on') {
        this.audioStatus = true;
        this.audioTog.isChecked = true;
        this.turnOnA.active = true;
        this.turnOffA.active = false;
        this.audioSrc.volume = 0.3;
    } else if (savedStatus === 'off') {
        this.audioStatus = false;
        this.audioTog.isChecked = false;
        this.turnOnA.active = false;
        this.turnOffA.active = true;
        this.audioSrc.volume = 0;
    }

    // Xử lý sự kiện khi người dùng nhấn nút âm thanh
    this.audioTog.node.on('toggle', () => {
        this.audioStatus = this.audioTog.isChecked;
        // Lưu trạng thái của âm thanh vào Local Storage
        sys.localStorage.setItem('audioStatus', this.audioStatus ? 'on' : 'off');

        // Thực hiện các hành động tùy thuộc vào trạng thái âm thanh
        if (this.audioStatus) {
            this.turnOnA.active = true;
            this.turnOffA.active = false;
            this.audioSrc.volume = 0.3;
        } else {
            this.turnOnA.active = false;
            this.turnOffA.active = true;
            this.audioSrc.volume = 0;
        }
    });
    }
    musicTogFunc(){
        this.onAudioQueue(2);
        this.musicStatus = this.musicTog.isChecked;
        sys.localStorage.setItem('musicStatus', this.musicStatus ? 'on':'off');
        if(this.musicStatus){
            this.turnOnM.active = false;
            this.turnOffM.active = true;
            this.musicSrc.volume = 0;
        }else{
            this.turnOnM.active = true;
            this.turnOffM.active = false;
            this.musicSrc.volume = 0.3;
        }
    }
}


